export const jwtToken = writable('');

export const csrfToken = writable('');



//     
function getCookie(name) {
  const value = `; ${document.cookie}`;
  const parts = value.split(`; ${name}=`);
  if (parts.length === 2) return parts.pop().split(';').shift();
}




export async function SecurityFetch(endpoint, method, data = null) {

  
  const token = get(jwtToken) || null;
  const csrfToken = getCookie('csrfToken') || null; //  CSRF  

  const options = {
    method: method,
    headers: {
      "Content-Type": "application/json",
      "d2rpvpjwtToken": token,
      "X-CSRF-Token": csrfToken,  // CSRF  X-CSRF-Token 
    },
    credentials: "include", //  
  };

  if (data) {
    options.body = JSON.stringify(data);
  }

  try {
    const response = await fetch(endpoint, options);
    if (!response.status===200) {
      throw new Error(`Request failed with status ${response.status}`);
    }
    return response;
  } catch (error) {
    console.error('Error during fetch:', error);
    throw error;
  }
}
