<script>
  import { onMount } from "svelte";
  import {
    nickname,
    mode,
    clickformopen,
    SecurityFetch
  } from "./store.js";
  import Challenge from "./Challenge.svelte";

  let email = "Loading";
  let rscore = "Loading";
  let bscore = "Loading";
  let lscore = "Loading";
  let wins = "Loading";
  let loses = "Loading";
  let clan = "Loading";
  let clanwin = "Loading";
  let clanlose = "Loading";
let clandraw = "Loading";
let challenge = null;
let challengeDate = null;

  //       
  async function fetchGameData() {
    const fetchData = async () => {
      const endpoint = $mode ? "/user_data_m" : "/user_data";
      const response = await SecurityFetch(endpoint,"GET")
      return response;
    };

    try {
      let response = await fetchData();

      if (response.ok) {
        const data = await response.json();

        if ($mode) {
          email = data.email;
          lscore = data.lscore;
          clan = data.clan;
          clanwin = data.clanwin;
          clanlose = data.clanlose;
          clandraw = data.clandraw;
          challenge = data.Challenge;
          challengeDate = data.ChallengeDate;
        } else {
        email = data.email;
        bscore = data.bscore;
        lscore = data.lscore;
        rscore = data.rscore;
        wins = data.countwin;
        loses = data.countlose;
      } }
    } catch (error) {
      console.error("    :", error);
      alert("    ");
    }
  }


  async function cancelChallenge() {
    const data = {mode: $mode, challenge: challenge}

    try{
    const response = await SecurityFetch("/cancel_challenge","DELETE",data);
    if (response.status===200)
{alert("    ")}

  }
catch (error) {
console.error(" ", error);
alert(" ");
}

  }


  async function CheckTimeOver() {
    const data = {mode: $mode, challenge: challenge}

    try{
    const response = await SecurityFetch("/check_challenge","DELETE",data);
    if (response.status===200)
{alert("       ")

} else if (response.status===400)
{
alert("      ")
}

  }
catch (error) {
console.error(" ", error);
alert(" ");
}

  }





  onMount(() => {
    fetchGameData();
  });
</script>

<div class="outline">
  <div class="info-flexbox">
    <div class="info-name"></div>
    <div class="info-data">
      {$mode ? $nickname.replace("_m", "") : $nickname}
    </div>
  </div>
  <div class="info-flexbox">
    <div class="info-name-data">{email}</div>
  </div>
  <div class="info-flexbox">
    <div class="info-name">
      <button on:click={() => clickformopen("changepw")}> </button>
    </div>
    <div class="info-data">
      <button on:click={() => clickformopen("changeemail")}> </button>
    </div>
  </div>{#if $mode} <div class="info-flexbox">
    <div class="info-name"></div>
    <div class="info-data">{lscore}</div>
  </div>
  <div class="info-flexbox">
    <div class="info-name"></div>
    <div class="info-data">{clan}</div>
  </div>
  {#if clan==="none"} 
  <div class="info-data">
   <button on:click={() => clickformopen("clanjoin")}></button>
 </div>
 {/if}
  <div class="info-flexbox">
    <div class="info-name"></div>
    <div class="info-data">{clanwin}/{clandraw}/{clanlose}</div>
  </div>
  <div class="info-flexbox">
    <div class="info-name"> </div>
    <div class="info-data">{challenge ? challenge.replace("_m","") : ""}</div>
  </div>
  <div class="info-flexbox">
    <div class="info-name"> </div>
    <div class="info-data">{challengeDate ? challengeDate : ""}</div>
  </div>
  <div class="info-flexbox">
    <div class="info-name"> <button on:click={() => cancelChallenge()}> </button></div>
    <div class="info-data"> <button on:click={() => CheckTimeOver()}> </button></div>
  </div>


  {:else}
     
   <div class="info-flexbox">
    <div class="info-name"></div>
    <div class="info-data">{bscore}</div>
  </div>
  <div class="info-flexbox">
    <div class="info-name"></div>
    <div class="info-data">{lscore}</div>
  </div>
  <div class="info-flexbox">
    <div class="info-name"></div>
    <div class="info-data">{rscore}</div>
  </div>
  <div class="info-flexbox">
    <div class="info-name">/</div>
    <div class="info-data">{wins}/{loses}</div>
  </div>
{/if}

  <div class="info-data">
    <button on:click={() => clickformopen("deleteaccount")}> </button>
  </div>
</div>

<style>
  .outline {
    margin-top: 20px;
  }
  .info-flexbox {
    display: flex;
  }
  .info-name {
    width: 300px;
    text-align: center;
    font-size: 20px;
  }
  .info-data {
    width: 300px;
    text-align: center;
    font-size: 20px;
  }
  .info-name-data {
    text-align: center;
    width: 600px;
    font-size: 20px;
  }
</style>
