<script>
    import { onMount } from "svelte";
    import {SecurityFetch, mode} from "./store";
    let recordData = [];
    let loading = true;
  
    //    
    async function fetchGameData() {
  
      const endpoint = $mode ? "/challenge_data_m" : "/challenge_data"; 
  
      try {
        const response = await SecurityFetch(endpoint, "GET");
        if (!response.ok) throw new Error(` : ${response.status}`);
        recordData = await response.json();
        loading = false;
      } catch (error) {
        console.error("  :", error.message);
        alert("    ");
      }
    }
  
    //  /  
    async function handleRecord(action, challenger) {
      const endpoint = action === "win" ? "/challengewin" : "/challengelose";
      const data = { challenger: challenger, mode: $mode };
  
      try {
        const response = await SecurityFetch(endpoint,"POST",data);
  
        if (response.ok) {
          alert(`${action === "win" ? " ,    ." : "   "} `);
          fetchGameData();
        } else {
          throw new Error(` : ${response.status}`);
        }
      } catch (error) {
        console.error(`${action === "approve" ? "" : ""} :`, error.message);
        alert(` : ${error.message}`);
      }
    }
  
    //      
    onMount(fetchGameData);
  </script>
  
  <table>
    <tr>
      <th class="record-table-date"></th>
      <th class="record-table-loser"></th>
      <th class="record-table-score"></th>
    </tr>
    {#if loading}
      <tr><td colspan="4" class="text-center"> ...</td></tr>
    {:else if recordData.length === 0}
      <tr><td colspan="4" class="text-center"> </td></tr>
    {:else}
      {#each recordData as row}
        <tr>
          <td>{new Date(row.ChallengeDate).toLocaleDateString()}</td>
          <td>{$mode? row.Nickname.replace("_m","") : row.Nickname}</td>
          <td>
            <button on:click={() => handleRecord("win", row.Nickname)}></button>
            <button on:click={() => handleRecord("lose", row.Nickname)}></button>
          </td>
        </tr>
      {/each}
    {/if}
  </table>
  
  <style>
    table {
      width: 90%;
      margin-top: 20px;
    }
    .record-table-date {
      width: 15%;
    }
    .record-table-loser {
      width: 40%;
    }
    .record-table-score {
      width: 15%;
    }
    .record-table-ok {
      width: 20%;
    }
  </style>
  